/*
 *  Featured Content 7
 *    Styles for the featured content 7  section.
 *
 *  Version:
 *    1.0.0 - 2021/11/11
 */
.c-featured-content-7 {
  background: $s-color--background-impact;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    color: $s-color--border-invert;
    padding: 4rem $s-gutter--edge-mobile 6.25rem;
  }

  &__cta-container {
    margin-top: 1.5rem;
  }

  &__cta-btn {
    width: 100%;
  }

  &__image-container {
    padding-bottom: 83%;
  }

  &__title {
    margin: 2.0625rem 0 1.375rem;
  }

  @include t-media-query($s-small-up) {

    &__inner {
      display: flex;
      align-items: center;
      gap: $s-gutter--edge-mobile;
    }

    &__image-wrapper {
      width: 54%;
    }

    &__content-container {
      width: 45%;
    }

    &__cta-btn {
      width: initial;
    }
  }

  @include t-media-query($s-medium-up) {
    
    &__inner {
      padding: 5.5rem 10%;
      gap: 2.5rem;
    }

    &__image-wrapper {
      width: 48.275%;
    }
  }

  @include t-media-query($s-xlarge-up) {

    &__inner {
      padding: 8.875rem 21%;
    }

    &__title {
      margin-top: 0;
      margin-bottom: 1.9375rem;
    }

    &__cta-btn {
      width: 50%;
      margin-top: 2rem;
    }
  }
}